import R from 'ramda';
import {FETCH_GAMES_SUCCESS, LOAD_MORE_GAMES_SUCCESS, SEARCH_GAME} from '../actionTypes';
const initialState = {
    ids: [],
    search: ''
};

export default (state = initialState, {type, payload}) => {
    switch (type) {
        case FETCH_GAMES_SUCCESS:
            return R.merge(state, {
                ids: R.pluck('id', payload)
            });

        case LOAD_MORE_GAMES_SUCCESS:
            const ids = R.pluck('id', payload);
            return R.merge(state, {
                ids: R.concat(ids, state.ids)
            });

        case SEARCH_GAME:
            return R.merge(state, {
                search: payload
            });
            
        default:
            return state;
    }
}


